import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-crc16',
  templateUrl: './crc16.component.html',
  styleUrls: ['./crc16.component.css']
})
export class Crc16Component implements OnInit {

  data:any = "";

  constructor() {

   }


  ngOnInit() {
      
      var crc16 = this.generateCRC(this.data);
      console.log("crc result: " + crc16);
  }

  generateCRC(data:any){
    var crc:any = 0x00;
    var j:any, i:any;
    var c:any;
    var result:string = "";

    data = data.split(",");

    for (i = 0; i < data.length; i++) {

      c = data[i];

      if (c > 255) {
        throw new RangeError();
      }

      crc = crc ^ (c<<8) ;

      for(j=0;j<8;j++){ 
        if(crc & 0x8000) 
          crc = ((crc<<1)^0x1021);
        else 
          crc = crc << 1;
        }
    }

    result = ((crc ^ 0) & 0xFFFF).toString(16);

    //result = "0x"+result.substr(0,2) + "," + "0x"+result.substr(2,2)
    result = this.formatHEX(result.substr(0,2)) + "," + this.formatHEX(result.substr(2,2));
    return "," + result.toLocaleUpperCase();

  }

  formatHEX(value:string){
    value = value.toUpperCase();

      if(value.length<2)
        value = "0"+value;
      
          
      return "0x" + value;  
  }
}



