import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MdButtonModule, MdCardModule, MdMenuModule, MdToolbarModule, MdIconModule, MdNativeDateModule, MdDatepickerModule, MdSliderModule, MdGridListModule, MdInputModule } from '@angular/material';
import { HttpClientModule} from '@angular/common/http';
import { NgxfUploaderModule } from 'ngxf-uploader';
import { DecimalPipe } from '@angular/common';


import { AppComponent } from './app.component';
import { Crc16Component } from './crc16/crc16.component';
import { FileuploaderComponent } from './fileuploader/fileuploader.component';
import { LedcommandsComponent } from './ledcommands/ledcommands.component';
import { RouterModule, Routes } from '@angular/router';
import { UpdatePricesComponent } from './update-prices/update-prices.component';

import 'hammerjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/filter';
 
import 'rxjs/add/observable/of';
import 'rxjs/add/observable/throw';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full'},
  { path: 'home/:id', component: LedcommandsComponent},
  { path: 'updatePrices', component: UpdatePricesComponent },  
  { path: '**', component: LedcommandsComponent}
 ];

@NgModule({
  declarations: [
    AppComponent,
    Crc16Component,
    FileuploaderComponent,
    LedcommandsComponent,
    UpdatePricesComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MdButtonModule, 
    MdCardModule, 
    MdMenuModule, 
    MdToolbarModule, 
    MdIconModule,
    MdGridListModule,
    MdInputModule,
    MdSliderModule,
    MdDatepickerModule,
    MdNativeDateModule,
    NgxfUploaderModule.forRoot(),
    RouterModule.forRoot(routes, {useHash: true})
  ],
  providers: [DecimalPipe],
  bootstrap: [AppComponent]
})
export class AppModule { }



